*{
  margin: 0;
  padding: 0;
}
body {
  overflow-x: hidden;
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  margin: 0;
  padding: 0;
}

.App{
  overflow-x: hidden;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
.page1{
  background-color: #FFE79C;
  padding-bottom: 70px;
  width: 100%;
}
.nav{
   height: 7vh;
   z-index: 100;
   position:fixed;
   width: 100%;
   background-color: none;
   padding: 20px;
   padding-top:0 ;
   align-items: center;

}
.nav ul{
  height: 100%;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  list-style: none;
}
.nav ul li{
  display: inline-block;
}
.nav ul li img{
  height: 140%;
}
.nav .contact{
  background-color: #1C2042;
  color: white;
  font-size: 12px;
  font-weight: 600;
  padding: 10px;
  margin-right: 40px;
  cursor: pointer;
  margin-top: 20px;
}
.nav ul .logol{
  display: flex;
  flex-direction: row;  
  align-items: center;
  font-family: 'Noto Sans JP', sans-serif;
  font-weight: 600;
  font-size: 130%;
  letter-spacing: 5px;
  margin-top: 20px;
}
.graphic{
  display: flex;
  flex-direction: column;
  align-items: center;
}
.board{
 margin-top: 20vh;
 background-color: #FEC441;
 font-family: 'Noto Sans JP', sans-serif;
 width: fit-content;
 padding: 2%;
 font-size: 240%;
 padding-left: 70px;
 padding-right: 70px;
 /* -webkit-text-stroke: 1px lightyellow; */
 animation: 5s colorc infinite;
 color: #ffe056 ;
 box-shadow: 0 1px 2px 0 rgba(0, 0, 0, 0.2), 0 0px 0px 0 rgba(0, 0, 0, 0.19);
z-index: 1;
}
@keyframes colorc {
   from{-webkit-text-stroke: 1px black;}
   to{-webkit-text-stroke: 1px lightyellow;}
}
.foot{
  display: flex;
}
.left-foot{
  background-color: #FEC441;
  display: flex;
  width: 30px;
  height: 140px;
  margin-right:50px;
  box-shadow: 0 1px 2px 0 rgba(0, 0, 0, 0.2), 0 0px 0px 0 rgba(0, 0, 0, 0.19);
}
.left-foot img{
  align-self: flex-end;
  width: 250%;
  margin-left: -20px;
}
.right-foot{
  background-color: #FEC441;
  width: 30px;
  height: 140px;
  display: flex;
 margin-left: 210px;
 box-shadow: 0 1px 2px 0 rgba(0, 0, 0, 0.2), 0 0px 0px 0 rgba(0, 0, 0, 0.19);
}
.right-foot img{
  align-self: flex-end;
  width: 250%;
  margin-left: -20px;
}
.grass{
  z-index: -99;
}
.football{
  display: flex;
  justify-content: space-between;
  height: 30vh;
  width: 100%;
  align-items: baseline;
  background-image: url('./assets/grass.png');
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  background-position-y: 40%;
}
.football img { 
  height: 100%;
   
}
.ball-container{
  flex: 1;
  float: right;
  float: right;
  display: flex;
  padding-left: 975px;
  animation: 5s movelr infinite;
}
@keyframes movelr {
  from{ padding-left: 0px;}
  to{ padding-left: 935px;}
}
.ball{
   
}
.about{
  padding-left: 12vw;
}
.about .title{
  font-weight: 700;
  font-size: 90px;
  color: #1C2042;
}
.about .images{
  display: flex;
  flex-wrap: wrap;
}
.about .images .image{
  height: 700px;
  width: 35vw;
  margin-right: 60px;
}
.about .images .image-holder:nth-child(1) .image{
  background-color: #B22321;
}
.about .images .image-holder:nth-child(2) .image{
  background-color: #E3BA3D;
}
.about .images .image-holder:nth-child(3) .image{
  background-color: #72C5D9;
}
.about .images .image-holder:nth-child(4) .image{
  background-color: #D33C31;
}
.about .images .image-holder:nth-child(even){
  margin-top: 50px;
}
.about .images .image-holder{
  font-size: 10px;
  line-height: 1;
}
.about .images .image-holder .image img{
  width: 100%;
  bottom: 0;
}
.about .images .image-holder .image{
  display: flex;
  /* flex-direction: column; */
  /* align-items: flex-end; */
  overflow: hidden;
}
.about .images .image-holder .name{
  font-weight: 800;
}

.page2{
  z-index: 100;
  margin-top: 0;

}
.quote{
  background-color: #F9EFF1;
  padding-left: 12vw;
  font-weight: 700;
  font-size: 90px;

  color: #1C2042;
}
.quote p{
  width: 70vw;

}
.hero{
  height: 100vh;
  display: flex;
  flex-direction: column;
  padding-right: 80px;
  justify-content: center;
}
.hero .image{
  width: 70%;
  align-self: center;
}
.hero .image img{
  width: 80%;
}
.hero .herob{
 align-self: flex-end;
 font-weight: 700;
 font-size: 30px;
 color: #1C2042;

}
.hello{
  display: flex;
}
.hello .say{
  background-color: #1C2042;
  color: white;
  font-size: 25px;
  height: fit-content;
  width: fit-content;
  padding:10px;
}
.arrow{
  font-size: 30px;
}
.hello div svg{
  height: 50px;
  width: 50px;
}
.cases{
  padding-left: 80px;
  padding-bottom: 50px;
}
.cases .title{
  font-weight: 700;
  font-size: 70px;
  color: #1C2042;
  
}
.case-card{
  display: flex;
  justify-content: space-between;
  width: 90%;
  border-top:1px solid gray;
  padding-top: 20px;
  margin-top:50px;
}
.case-card .name{
  font-size: 30px;
}
.case-card{
  font-weight: 500;
}
.case-card .detail{
  color: gray;
}
.services{
  background-color: #1C203E;
  padding: 80px;
  height: 150vh;
}
.services .title{
  font-size: 70px;
  font-weight: 600;
  color: white;
}
.service-card{
  width: 20vw;
  margin-bottom: 80px;
}
.service-card .title{
  font-size: 25px;
  font-weight: 600;
  color: white;
}
.service-card .detail{
  margin-top: 14px;
  color: gray;
  font-weight: 500;
}
.service{
  display: flex;
  width: 50vw;
  flex-wrap: wrap;
  justify-content: space-between;
  float:right;
}
.good-bye{
  height: 100vh;
}
.good-bye img{
  width: 100%;
  height: 100%;
}
.footer{
  background-color: #1C2042;
  color: white;
  padding: 80px;
}
.footer .contact{
  display: flex;
  width: 80vw;
  justify-content: space-between;
}
.footer .contact .number{
  font-size: 50px;
  font-weight: 700;
}
.footer .contact .address .add{
  font-size: 40px;
  font-weight: 700;
  margin-bottom: 20px;
}
.footer .contact .address .follow{
  font-weight: 600;
  margin-bottom: 10px;
}
.footer .contact .address .logo{
  font-size: 25px;
  color: white;
  word-spacing: 10px;
}
a{
  color: white;
  text-decoration: none;
}
a:hover{
  color: gray;
  
}
.copy{
  font-weight: 500;
  color: gray;
   display: flex;
   justify-content: space-between;
   width: 62vw;
   margin-top: 150px;
}
.more-detail{
  width: 60vw;
  float:right;
  margin-right: 80px;
  padding-bottom: 50px;
}
.more-detail img{
  width: 100%;
}
.more-detail .details{
  width: 70%;
  margin-left: 20px;
  font-weight: 700;
  font-size: 20px;
  color: #1C2042;
}
.contact-page{
  background-color: #1C2042;
  width: 20vw;
  height: 100vh;
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  padding: 20px;
  padding-left: 50px;
  padding-right: 50px;
}
.contact-page .title{
  color: white;
  font-size: 40px;
  font-weight: 700;
  margin-bottom: 40px;
}
.contact-page input{
  flex: 1;
  width: 100%;
  background:none;
  border: none;
  border-bottom: 1px solid gray;
  font-size: 12px;
  font-weight: 800;
  margin-bottom: 10px;
  padding-bottom: 10px;
}
.submit{
   background: white;
   color: #1C2042;
   width: 100%;
   font-weight: 800;
   height: 40px;
   margin-top: 10px;
   cursor: pointer;
}
.close{
  border-radius: 50px;
  padding: 40px;
  padding-right: 50px;
  width: 40%;
  float: right;
  color: #1C203E;
  font-weight: 700;
  background-color: #FFE79C;
  border: none;
  margin-left: 60%;
  cursor: pointer;
}
.contact-page input:active{
  border: none;
}
.contact-page input:focus{
  outline: none;
  color: white;
}
.nav .logos{
  display: none;
}
